import React, { useState } from 'react';
import SurveyForm from './components/SurveyForm';
import MoodChart from './components/MoodChart';
import './App.css';

function App() {
  const [userData, setUserData] = useState([]);

  const handleFormSubmit = (data) => {
    setUserData([...userData, { ...data, date: new Date().toISOString() }]);
  };

  const analyzeData = (data) => {
    const gpaPrediction = data[data.length - 1].gpa > 2.0 ? 'GPA stable or increasing' : 'GPA may decrease';
    const burnoutRisk = data.some(entry => entry.homeworkHours > 5 || entry.extracurricularHours > 2) ? 'Risk of burnout' : 'Low burnout risk';

    return { gpaPrediction, burnoutRisk };
  };

  const predictions = analyzeData(userData);

  return (
    <div className="App">
      <h1>Student Wellness Tracker</h1>
      <SurveyForm onSubmit={handleFormSubmit} />
      {userData.length > 0 && (
        <>
          <h2>Predictions:</h2>
          <p>{predictions.gpaPrediction}</p>
          <p>{predictions.burnoutRisk}</p>
          <MoodChart data={userData} />
        </>
      )}
    </div>
  );
}

export default App;
